import { escapeWithRegexp, unescapeWithRegexp } from '../../../utils/escape';

export default {
  id: "mapNavigation",
  label: "Map Navigation",
  fields: [
    { name: "name", label: "Name", widget: "string" },
    { name: "imageSrc", label: "Background Image", widget: "image" },
    {
      name: "areas", 
      label: "Areas", 
      widget: "list", 
      fields: [
        { 
          name: "shape", 
          label: "Shape", 
          widget: "select",
          options: [ 
            { label: "Rectangle", value: "rect" },
            { label: "Circle", value: "circle" },
            { label: "Polygohn", value: "poly" },
            { label: "Default", value: "default" }
          ]
        },
        { name: "name", label: "Name", widget: "string" },
        { name: "coords", label: "Coordinates", widget: "string" },
        { name: "href", label: "Link To URL", widget: "string" },
        { name: "alt", label: "Alternate Text", widget: "string" },
      ]
    }
  ],
  pattern: new RegExp(`:::md-component AboutTheAuthor headshot="(.*?)" name="(.*)" bio="(.*?)" chapterDescription="(.*?)" interviewDescription="(.*?)"`),
  fromBlock: function(match) {
    return {
      headshot: unescapeWithRegexp(match[1]),
      name: unescapeWithRegexp(match[2]),
      bio: unescapeWithRegexp(match[3]),
      chapterDescription: unescapeWithRegexp(match[4]),
      interviewDescription: unescapeWithRegexp(match[5]),
    };
  },
  toBlock: function(obj) {
    var MAP = {
      name: 'my-map',
      areas: obj.areas
    };

    return `:::md-component MapNavigation imageSrc="${escapeWithRegexp(obj.imageSrc)}"` + 
      ` map={${JSON.stringify(MAP)}}`;
  },
  toPreview: function(obj) {
    return `About the Author ${obj.name}`;
  }
};